.post-section {
    margin: 96px auto;
  }
  
  .post-items {
    box-shadow: 0px 0px 9.4px 0.6px rgb(152 152 152 / 63%);
    border-radius: 4px;
  }
  
  .post-content {
    padding: 20px;
  }
  
  .post-item {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  
  #first-item {
    background: linear-gradient(to right bottom, #fecdec, #9896f0);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  #second-item {
    background: linear-gradient(to right, #fef1ce, #9df095);
    display: block;
  }
  
  .post-item-wrapper {
    display: flex;
    justify-content: center;
  }
  
  #second-item h2 {
    text-align: center;
    margin: 0;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  
  .post-second-list {
    padding: 0;
    margin: 0;
  }
  
  .post-second-list-item {
    display: flex;
    margin: 1.5rem auto;
  }
  
  .post-second-list-item:nth-child(even) {
    flex-direction: row-reverse;
  }
  
  .post-second-image-container {
    width: 300px;
  }
  
  .post-second-text-container {
    width: 300px;
    display: flex;
    align-items: center;
  }
  
  .post-second-text-container p {
    padding-right: 1rem;
    padding-left: 1rem;
  }

  .block-fome{
    padding-right: 10px;
  }
  
  .block-button {
    align-items: center;
    display: flex;
    justify-content: center;
  }
  
  .block-button-link {
    background-color: white;
    text-decoration: none;
    border-radius: 17px;
    padding: 1rem;
  }
  
  .block-button-link:hover {
    text-decoration: underline;
  }
  
  .block-button-link strong {
    color: black;
  }
  
  #third-item {
    background: linear-gradient(to right bottom, #fec970, #cb5ab9, #4358cf);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .icons {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  
  .icon-container {
    margin: auto 1rem;
    width: 150px;
    height: 150px;
  }
  
  .icon-container img {
    width: 100%;
    height: 100%;
  }
  
  .block-button-row {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .fifth-post-button-container {
    background: linear-gradient(to right bottom, #fec970, #cb5ab9, #4358cf);
    text-decoration: none;
    border-radius: 17px;
    padding: 1rem;
    margin: 0.5rem;
  }
  
  .row-button-link {
    color: white;
    text-decoration: none;
  }
  
  .row-button-link:hover {
    text-decoration: underline;
    color: #745801;
  }
  